@import "./join/join";
@import "./vacancies/vacancies";
@import "./e-form/e-form";
@import "./communication/communication";

.main {
  width: 100%;
  max-width: $max-width;
  margin: 0 auto;

  &__wiew-all {
    width: 100%;
    display: flex;
    justify-content: center;

    & button {
      @include fonts-style ("Signika", bold, 14px, normal, 130%, white);
      border: solid 1px #FF1500;
      border-radius: 100px;
      padding: 13px 25px;
      cursor: pointer;
      outline: none;
  
      background: -webkit-linear-gradient(90deg, #FF1500 0%, #FE7000 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;

      transition: transform 1s;

      &:hover {
        transform: scale(1.05);
      }
    }
  }

  &__enter {
    margin: 65px 0;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  &__e-form {
    max-width: 550px;
  }
}

@media only screen and (max-width: 685px) {
  .main {
    &__wiew-all {
      display: none;
    }
  }
}
